.top-nav-bar {
  display: flex;
  justify-content: space-between;

  .logo {
    height: 30px;
    padding: 2.5px 0;
  }

  .icons {
    svg {
      height: 25px;
      padding: 7.5px;
    }

    a:first-child {
      margin-right: 10px;
    }
  }

  .notification-circle {
    display: none;
  }

  .new-notifications {
    .notification-circle {
      display: inline;
      fill: var(--notification-circle-color);
    }
  }

  button {
    padding: 0;
    background-color: transparent;
  }
}
