@font-face {
  font-family: 'AbrilText';
  src: url('../../public../../public/fonts/subset-AbrilTextLt.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'AbrilText-Italic';
  src: url('../../public/fonts/subset-AbrilTextLt-Italic.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Raleway';
  src: url('../../public/fonts/subset-Raleway-SemiBold.woff2') format('woff2');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Raleway';
  src: url('../../public/fonts/subset-Raleway-Regular.woff2') format('woff2');
  font-weight: 40;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Raleway';
  src: url('../../public/fonts/subset-Raleway-Medium.woff2') format('woff2');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

.erik-olsson {
  --primary-color: #FFC774;
  --secondary-color: #FEEFDB;
  --button-background-color: var(--primary-color);
  --notification-circle-color: var(--primary-color);
  --accent-text-color: #F39200;
  --header-font: 'AbrilText', 'Georgia', sans-serif;
  --body-font: 'Raleway', sans-serif;
}

.skandiamaklarna {
  --primary-color: #1F3043;
  --secondary-color: #E9CFC2;
  --button-background-color: var(--primary-color);
  --notification-circle-color: var(--secondary-color);
  --header-font: 'AbrilText', 'Georgia', sans-serif;
  --body-font: 'Raleway', sans-serif;
}
