:root {
  --primary-color: rgb(28, 95, 11);
  --secondary-color: rgb(43, 202, 141);
  --main-bg-color: #fff;
  --secondary-bg-color: #f9f9f9;
  --main-text-color: #1E1E1E;
  --secondary-text-color: #575756;
  --accent-text-color: #306b00;

  --button-background-color: var(--primary-color);
  --button-text-color: #fff;

  --notification-circle-color: var(--primary-color);

  --header-font: sans-serif;
  --body-font: serif;
  --font-size: 1rem;
  --font-size-s: 0.875rem;
  --font-size-xs: 0.75rem;
}
