.estate-link {
  display: flex;
  text-decoration: none;
  color: var(--main-text-color);

  img {
    width: 90px;
    border-radius: 10px;
    object-fit: cover;
    margin-right: 10px;
  }

  .information {
    width: 100%;
    background-color: var(--secondary-bg-color);
    padding: 20px;
    border-radius: 10px;
  }

  .address {
    font-weight: 700;
  }

  .price-text, .date-sold {
    font-size: var(--font-size-xs);
    color: var(--secondary-text-color);
  }

  .price-difference, .in-progress {
    font-size: var(--font-size-xs);
    color: var(--accent-text-color);
  }
}
