.bottom-nav-bar {
  display: flex;
  justify-content: space-around;

  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;

  padding: 0.5rem;

  .documents-space {
    fill: #ffffff;
  }

  .documents-tab {
    fill: #000;
  }

  .active {
    .process-icon {
      fill: var(--button-background-color);

      .process-lines {
        stroke: var(--button-background-color);
      }
    }

    .checklists-icon {
      background-color: var(--button-background-color);
      border-radius: 2px;
      stroke: #ffffff;

      .checklists-lines {
        stroke: #ffffff;
      }

      .checklists-circles {
        fill: #ffffff;
      }
    }

    .documents-icon {

      .documents-tab-lines {
        fill: #ffffff;
        stroke: var(--button-background-color);
      }

      .documents-tab {
        fill: #ffffff;
      }

      .documents-space {
        fill: var(--button-background-color);
      }
    }

    .offers-icon {
      fill: var(--button-background-color);
      .offers-lines {
        stroke: var(--button-background-color);
      }

      .offers-circle {
        fill: #ffffff;
        stroke: var(--button-background-color);
      }
    }
  }
}
