@import '../styles/variables.scss';

.estate-info {
  display: flex;
  justify-content: space-between;
  padding: 10px;

  .estate-address {
    font-weight: 700;
  }

  .estate-area, .starting-price {
    font-size: var(--font-size-xs);
    color: #9C9C9C;
  }

  .estate-price {
    font-size: var(--font-size-xs);
  }
}
